.footer-header {
  /* display */
  padding: 0.4em 1em;

  /* font */
  font-size: 1.3em;
  line-height: 1.3;
  white-space: pre-line;
}

.footer-header > strong { font-size: 1.2em; }



.footer-body {
  margin-top: 0px;
}

.footer-desc { font-weight: 300; }

.footer-links {
  /* display */
  margin-top: 0.5cm;

}

.footer-link {
  /* display */
  padding-top: 0.3em;
  margin-right: 1100px;
  /* font */
  font-weight: 600;
}
.footer-link2 {
  /* display */
  padding-top: 0.3em;
  /* font */
  font-weight: 600;
  text-align: right;
}

.footer-copyright {
  /* position */
  position: absolute;
  bottom: 1em;
  left: 0;
  right: 0;

  /* font */
  text-align: center;
}

/* ============================== */
/* ---------- RESPONSIVE -------- */
/* ============================== */

@media (min-width: 480px) {
  .footer-header {
    /* display */
    padding: 0.35em 0.8em;

    /* font */
    font-size: 1.4em;
    line-height: 1.3;
  }

  .footer-body {
    /* display */
    padding-right: 3em;
  }

  .footer-links {
    /* display */
    display: -webkit-flex;
    display: flex;
    height: 8em;

    /* font */
    text-align: left;

    /* flex */
    -webkit-flex-flow: column wrap;
            flex-flow: column wrap
  }
}




